import React, {Component} from 'react';
import {Grid, Cell} from 'react-mdl';

class Skills extends Component{

    render(){
        return(
            <Grid>
                 <Cell col={9} >
                 <p className='resume-elem1' >{this.props.os}</p>
                 <p>{this.props.os2}</p>
                 <p className='resume-elem1' >{this.props.programminglanguages}</p>
                 <p>{this.props.programminglanguages2}</p>
                 <p className='resume-elem1' >{this.props.frameworks}</p>
                 <p>{this.props.frameworks2}</p>
                 <p className='resume-elem1' >{this.props.databases}</p>
                 <p>{this.props.databases2}</p>
                 <p className='resume-elem1' >{this.props.devopstools}</p>
                 <p>{this.props.devopstools2}</p>
                 <p className='resume-elem1' >{this.props.tools}</p>
                 <p>{this.props.tools2}</p>
                 <p className='resume-elem1' >{this.props.videoeditingsoftware}</p>
                 <p>{this.props.videoeditingsoftware2}</p>
                 <p className='resume-elem1' >{this.props.imageeditingsoftware}</p>
                 <p>{this.props.imageeditingsoftware2}</p>
                 <p className='resume-elem1' >{this.props.gameengines}</p>
                 <p>{this.props.gameengines2}</p>
                 </Cell>
            </Grid>
        )
    }


}
export default Skills;