import React, {Component} from 'react';
import {Grid, Cell} from 'react-mdl';


class Education extends Component{
    render(){
        return (
       <Grid>
           <Cell col={9} >
            <p><b>{this.props.startYear} - {this.props.endYear}</b></p>
            <p className='resume-elem1'>{this.props.schoolName}</p>
            <p>{this.props.schoolName2}</p>
            <p className='resume-elem1'>{this.props.department}</p>
            <p>{this.props.department2}</p>
            <p className='resume-elem1'>{this.props.mastersname}</p>
            <p>{this.props.mastersname2}</p>
            <p className='resume-elem1'>{this.props.thesistitle}</p>
            <p>{this.props.thesistitle2}</p>
           </Cell>
       </Grid>
        )
    }
}

export default Education;