import React, {Component} from 'react';
import { Grid, Cell } from 'react-mdl';
import pic from './circle-cropped.png';

class LandingPage extends Component{
    
    render(){
        return(
            <div>
             <Grid className="landing-grid">
                <Cell col={12}>
                <img 
                src={pic}
                alt="avatar"
                className="avatar-img"
                />

                <div className="banner-text">
                    <h1>MSc in Interactive Software and Hardware Technologies</h1>
                
                <hr/>

                <p>JAVA | C | C++ | C# | Python | HTML | CSS | XML | PHP | JAVASCRIPT/jQuery | SQL</p>

                <div className="social-links">

                    <a href="https://www.linkedin.com/in/kyriaki-karypidou-764a37102/" rel="noopener noreferrer" target="_blank">
                        <i className="fa fa-linkedin-square" aria-hidden="true"/>
                    </a>
                    
                    <a href="https://github.com/kirki333" rel="noopener noreferrer" target="_blank">
                        <i className="fa fa-github-square" aria-hidden="true"/>
                    </a>
                    
                </div>
                </div>
                </Cell>
             </Grid>   
            </div>

        )
    }
}

export default LandingPage;