import React, {Component} from 'react';
import {Grid, Cell} from 'react-mdl';

class Languages extends Component{

    render(){
        return(
            <Grid>
                <Cell col={4}>
                <p className='resume-elem1' >{this.props.english}</p>
                <p className='resume-elem1' >{this.props.spanish}</p>
                </Cell>

                <Cell col={8} className='resume-elem'>
                <p>{this.props.english2}</p>
                <p>{this.props.spanish2}</p>
                </Cell>
            </Grid>
        )
    }
}

export default Languages;