import React, {Component} from 'react';
import {Grid, Cell} from 'react-mdl';


class Bachelor_Education extends Component{
    render(){
        return (
       <Grid>
           <Cell col={9} >
            <p><b>{this.props.startYear} - {this.props.endYear}</b></p>
            <p className='resume-elem1' >{this.props.schoolName}</p>
            <p className='compress-education'>{this.props.schoolName2}</p>
            <p style= {{marginTop:'20px'}} className='resume-elem1'>{this.props.department}</p>
            <p>{this.props.department2}</p>
            <p className='resume-elem1'>{this.props.bachelorsname}</p>
            <p>{this.props.bachelorsname2}</p>
            <p className='resume-elem1'>{this.props.thesistitle}</p>
            <p>{this.props.thesistitle2}</p>
            <p className='resume-elem1'>{this.props.internship}</p>
            <p>{this.props.internship2}</p>
            <p className='resume-elem1'>{this.props.internshiptitle}</p>
            <p>{this.props.internshiptitle2}</p>
           </Cell>
       </Grid>
        )
    }
}

export default Bachelor_Education;