import React, {Component} from 'react';
import {Grid, Cell, List, ListItem, ListItemContent} from 'react-mdl';
import pic from './circle-cropped.png';

class Contact extends Component{
    
    render(){
        return(
            <div className="contact-body">
                <Grid className="contact-grid">
                    <Cell col={4}>
                        <h2>Kyriaki Karypidou</h2>
                        <img 
                         src={pic}
                         alt="avatar"
                         style={{height:'250px'}}
                        />

                        <p className = "intro-me" style={{margin: 'auto', paddingTop: '1em', textAlign:'justify'}}>
                        I have a masters degree in Interactive Hardware and Software Technologies and a bachelor degree in 
                        Information Engineering. Currently I am helping in some university projects and learning frameworks 
                        and new technologies to be up-to-date with the needs of the market. My main goal in life
                        is to help improve people's lives by creating applications with that purpose.
                        </p>
                        </Cell>
                    <Cell col={6}>

                    <h2>Contact Me</h2>
                    <hr/>

                    <div className="contact-list">

                        <List>
                            <ListItem>
                                <ListItemContent style={{fontSize: '30px', fontFamily: 'Anton'}}>
                            <i class="fa fa-envelope" aria-hidden="true"></i>
                            <p>kikikaryp@gmail.com </p>  
                                </ListItemContent>
                            </ListItem>
                            <ListItem>
                                <ListItemContent style={{fontSize: '30px', fontFamily: 'Anton', marginTop:'40px'}}>
                                <i class="fa fa-skype" aria-hidden="true"></i>
                                <p> kikikar1995</p>
                                </ListItemContent>
                            </ListItem>
                            <ListItem>
                                <ListItemContent style={{fontSize: '30px', fontFamily: 'Anton', marginTop:'40px'}}>
                                <i class="fa fa-github" aria-hidden="true"></i>
                                <p> https://github.com/kirki333</p>
                                </ListItemContent>
                            </ListItem>
                        </List>
                        </div>
                    </Cell>
                </Grid>

                
            </div>

        )
    }
}

export default Contact;