import React, { Component } from 'react';
import { Tabs, Tab, Grid, Cell, Card, CardImage, CardTitle, CardText, CardActions, Button, CardMenu, IconButton } from 'react-mdl';
import pic2 from './app_icon.png';
import pic3 from './food.png';
import pic4 from './healthy_me_logo_FINAL.png';

class Projects extends Component {

    constructor(props) {
        super(props);
        this.state = {
            activeTab: 0
        };
    }

    toggleCategories() {
        if (this.state.activeTab == 0) {
            return (

                <div className="container">
                    <div>
                    <Card shadow={5} className="car1">
                        <CardTitle style={{ color: '#ffff', WebkitTextStroke: '1px black', fontSize: '40px', height: '280px', background: `url(${pic2}) center / cover` }}> Trakapet</CardTitle>
                        <CardText style={{textAlign:'justify'}} className="cardelem">
                            <p> An Android application for finding and declaring lost or stray dogs.
                             A user makes a post about a stray dog he located by declaring its characteristics and the area he found it. During the publish the latitude and longitude is computed. Therefore, another user who might have lost it might use the application to look for the dog by specifying the area it got lost and come in contact by sharing their contact information within the app. A Rest API was needed in order to save and retrieve information about dogs as well as user’s information.
                           </p> <u>Technologies:</u>  <b> Android Studio, Java, XML, NOSQL, Firebase Realtime Database, Cloud storage, Authentication and Google Analytics, MVP pattern</b>
                        </CardText>
                    </Card>
                    </div> 
                    <div>
                    <Card shadow={5} className="car2">
                        <CardTitle style={{ color: '#ffff', WebkitTextStroke: '1px black',  height: '280px', background: `url(${pic3}) center / cover` }}> PickFood</CardTitle>
                        <CardText style={{ textAlign: 'justify' }} className="cardelem">
                            <p>An Android application that allows the user to enter ingredients of a food
                            and returns recipes with these ingredients. The application keeps track of the viewed recipes for future reference.
                            The app contains a Rest API for getting the recipes with the specified ingredients.
                        </p>
                            <u>Technologies:</u>
                            <b> Android Studio, Java, XML, Facebook SDK, Food2fork API</b>
                        </CardText>
                    </Card>
                    </div>
                    <div>
                    <Card shadow={5} className="car3">
                        <CardTitle style={{ color: '#ffff', WebkitTextStroke: '1px black', height: '280px', background: `url(${pic4}) center / cover` }}> Healthy Me</CardTitle>
                        <CardText style={{ textAlign: 'justify'}} className="cardelem">
                            <p>An Android gamified application for helping the user track the nutritional
                            values of foods and help him lose weight by teaching him about a healthy balanced diet.
                            The app contains two Rest API’s, one for retrieving the nutrition of food and another for
                            retrieving or sending nutrition values of a specific product’s barcode.
                        </p>
                            <u>Technologies:</u>
                            <b> Unity, C#, XML, Nutritionix API, OpenFoodFacts API, PHP, MySQL</b>
                        </CardText>
                    </Card>
                    </div>
                </div>

            )

        } else if (this.state.activeTab == 1) {

            return (
                <div className="container">
                    <div>
                    <Card shadow={5} className='car4'>
                        <CardTitle style={{ color: '#ffff', WebkitTextStroke: '1px black', textShadow: '1px 1px 2px black, 0 0 25px blue, 0 0 5px darkblue', height: '500px', background: 'url(https://camo.githubusercontent.com/5d6e2b2e17983a96c26d8f07332f70cf27170219/68747470733a2f2f626574616e6577732e636f6d2f77702d636f6e74656e742f75706c6f6164732f323031372f30342f494f545f496e7465726e65745f6f665f5468696e67735f323031372e6a7067) center / cover' }}> Digital PhotoBooth</CardTitle>
                        <CardText style={{ textAlign: 'justify'}} className="cardelem">
                            <p> Developing a digital photobooth using a Raspberry Pi and a DSLR camera. It is an emulation of the classic photobooth. The script has a simple UI with a button and when it gets clicked it shows a countdown, then three photos are taken consecutively and a photo strip is created. Then the photo is uploaded on a server and a QR code is created with the link of the photo on the webpage. The webpage is opening automatically.
                           </p> <u>Technologies:</u>  <b>Python</b>
                        </CardText>
                    </Card>
                    </div>

                    <div>
                    <Card shadow={5} className='car5'>
                        <CardTitle style={{ color: '#ffff', WebkitTextStroke: '1px black', textShadow: '1px 1px 2px black, 0 0 25px blue, 0 0 5px darkblue', height: '280px', background: `url(https://camo.githubusercontent.com/5d6e2b2e17983a96c26d8f07332f70cf27170219/68747470733a2f2f626574616e6577732e636f6d2f77702d636f6e74656e742f75706c6f6164732f323031372f30342f494f545f496e7465726e65745f6f665f5468696e67735f323031372e6a7067) center / cover` }}> Weather Data Analyser</CardTitle>
                        <CardText style={{ textAlign: 'justify' }} className="cardelem">
                            <p>Design and development of an IoT platform for analyzing weather data. The application makes three different requests for the temperature, humidity and pressure on the “OpenWeatherMap” server. Then it presents the values in real time by creating graphical representations. Also, the values from the server are getting saved in an SQLite database. In addition, the average value of Temperature and Humidity are printed. A description of more analytical weather data is also visible. All of the above, are shown in the UI of Node-red.
                        </p>
                            <u>Technologies:</u>
                            <b> Node-RED, SQLite, Javascript</b>
                        </CardText>
                    </Card>

                    </div>

                    <div>
                    <Card shadow={5} className='car6'>
                        <CardTitle style={{ color: '#ffff', WebkitTextStroke: '1px black', textShadow: '1px 1px 2px black, 0 0 25px blue, 0 0 5px darkblue', height: '500px', background: `url(https://camo.githubusercontent.com/5d6e2b2e17983a96c26d8f07332f70cf27170219/68747470733a2f2f626574616e6577732e636f6d2f77702d636f6e74656e742f75706c6f6164732f323031372f30342f494f545f496e7465726e65745f6f665f5468696e67735f323031372e6a7067) center / cover` }}> Android-based client for a fully-converged edge/fog based architecture for sensing and mobile crowdsensing</CardTitle>
                        <CardText style={{ textAlign: 'justify'}} className="cardelem">
                            <p>Modelled and designed a new client app including support for the communication with the
                            MEC/fog gateway and implementation of two new client-side functions: Context data services
                            to maintain and to know who is in the network, but also to characterize the sensed (physical)
                            surrounding environment, and Storage and aggregation of data to store data collected from heterogeneous
                            sensors or devices and to synchronize the whole locally aggregated data to the Mongo database. The client’s
                            side was developed on Android Studio using the Retrofit library to implement the REST API and the JmDNS library
                            to be able to discover services inside the WIFI network. In addition, Californium which is a Java implementation
                            of RFC7252 - Constrained Application Protocol for IoT Cloud services was added to the code to create the CoAP client
                            for communicating with the CoAP server located on the sensor as well as the Paho Android Service which is an MQTT client
                            library written in Java for developing applications on Android.
                        </p>
                            <p>Implementation of the designed client app as an Android mobile app, including the MEC/fog gateway side support as needed by developing the Fog gateway application that registers, starts and makes services visible to the network via mDNS. The services include communication with the Android client, the CoAP server and the MQTT publisher sensors as well as synchronization of the transmitted data to the local Mongo database.</p>
                            <p>
                                Built two different REST API’s, one for the proxy server using the Spark framework and one for the Android client using the Retrofit library.
                        </p>
                            <p>Thorough on-the-filed assessment of the realized functions to quantify the benefit of the introduced functions, and to record the CPU usage and bandwidth when using  HTTP, CoAP, MQTT and mDNS.</p>
                            <p>Stressing the MEC nodes and the Fog/proxy node -using the Ab Apache tool and incorporating the Poisson Distribution to the applications that were running the stress tests- to evaluate the feasibility
                            and performance of the Raspberry pi boards as well as of the complete implementation and the protocols used by establishing communication of all the cooperated parts together or independently: the Android
                        client (IoT consumer), the Fog Node (proxy) and MEC nodes (sensors). For stressing purposes, small java applications were created and exported as jar files to the Fog node and to the sensor nodes and run.</p>
                            <u>Technologies:</u>
                            <b> Android Studio, Java, Spark, Ab apache tool, MongoDB, Eclipse, IntelliJ, Retrofit, JmDNS, Californium, Mosquitto</b>
                        </CardText>
                        <CardActions border>
                            <Button colored href="https://www.researchgate.net/publication/329396857_DRIVE_Discovery_seRvice_for_fully-Integrated_5G_enVironmEnt_in_the_IoT" target="_blank">CAMAD Paper</Button>
                        </CardActions>
                    </Card>
                    </div>
                </div>
        

            )
        } else if (this.state.activeTab == 2) {

            return (

                <div className="container">
                    <Card className='cards' width='1000px'>
                        <CardTitle style={{ color: '#ffff', WebkitTextStroke: '1px black', textShadow: '1px 1px 2px black, 0 0 25px blue, 0 0 5px darkblue', height: '350px', width:'100%', background: 'url(https://miro.medium.com/max/640/1*XzIRJGujfqAiOV2EIQgR_Q.png) center / cover' }}>Open Source Python-powered RESTful API</CardTitle>
                        <CardText style={{ textAlign: 'justify' }} className="cardelem">
                            <p>Contributed to an Open Source Python-powered RESTful API “Simple Quiz API”. The repository was a simple flask app which runs a server with an API implemented. By sending HTTP requests on the server it is possible to create simple or multichoice questions with their answers and some information for each one. The questions are saved in json files. The user can also view all the questions and has the option to delete them. My contribution was about adding more types of requests to the rest API. I developed the code that showed all the categories of the simple and multichoice questions independently and how many questions each category contained.
                           </p> <u>Technologies:</u>  <b>Python, Flask</b>

                          <br/>
                          <br/>
                            <a className="cardelem" style={{fontSize:'50px', color:'black', opacity:'.8'}} href="https://github.com/kirki333/simple-quiz-api" rel="noopener noreferrer" target="_blank">
                                <i className="fa fa-github-square" aria-hidden="true" />
                            </a>
                        
                        </CardText>

                       
                    </Card>
                </div>


            )

        } else if (this.state.activeTab == 3) {

        }

    }

    render() {
        return (
            <div className="category-tabs">
                <Tabs activeTab={this.state.activeTab} onChange={(tabId) => this.setState({ activeTab: tabId })} ripple>
                    <Tab>Android Apps</Tab>
                    <Tab>IoT Apps</Tab>
                    <Tab>Web Applications</Tab>
                </Tabs>

                <section>
                        <div className="content">{this.toggleCategories()}</div>
                </section>
            </div>

        )
    }
}

export default Projects;