import React, {Component} from 'react';
import {Grid, Cell} from 'react-mdl';
import pic from './circle-cropped.png';
import Education from './education';
import Bachelor_Education from './bachelor_education';
import Skills from './skills';
import Languages from './languages';

class Resume extends Component{
    
    render(){
        return(
            <div>
                <Grid>
                    <Cell col={4}>
                        <div style={{textAlign: 'center', marginTop:'30px'}}>
                            <img src={pic} 
                            alt="avatar"
                            style={{height: '200px'}}
                            />
                        </div>
                        <h2>Kyriaki Karypidou</h2>
                        <h4 style={{color: 'grey'}}>MSc in Hardware and Software Technologies</h4>
                        <hr style={{borderTop: '3px solid #833fb2', width: '50%'}}/>
                        <p style={{textAlign: 'justify'}}> I have a masters degree in Interactive Hardware and Software Technologies and a bachelor degree in 
                        Information Engineering. Currently I am helping in some university projects and learning frameworks 
                        and new technologies to be up-to-date with the needs of the market. My main goal in life
                        is to help improve people's lives by creating applications with that purpose.</p>
                        <hr style={{borderTop: '3px solid #833fb2', width: '50%'}}/>
                        <h5>Email</h5>
                        <p>kikikaryp@gmail.com</p>
                        <hr style={{borderTop: '3px solid #833fb2', width: '50%'}}/>

                    </Cell>
                    <Cell className="resume-right-col" col={8}>
                    <h2>Education</h2>
                    <Education 
                    startYear = {2018}
                    endYear = {2020}
                    schoolName = "Academic Institution"
                    department = "Department"
                    mastersname = "Postgraduate Study Programme"
                    thesistitle = "Master Thesis"
                    schoolName2 = "Aristotle University of Thessaloniki (A.U.Th.)"
                    department2 = "School of Informatics"
                    mastersname2 = "Interactive Hardware and Software Technologies"
                    thesistitle2 = "EDUCATIONAL MOBILE APPLICATION FOR A HEALTHY DIET"
                    />

                    <Bachelor_Education
                    startYear = {2013}
                    endYear = {2018}
                    schoolName = "Academic Institution"
                    department = "Department"
                    bachelorsname = "Degree Title"
                    thesistitle = "Thesis Title"
                    internship = "Internship"
                    internshiptitle = "Internship Title"

                    schoolName2 = "International Hellenic University (former Alexander Technological Educational Institute of Thessaloniki)"
                    department2 = "Information Technology"
                    bachelorsname2 = "Information Engineer"
                    thesistitle2 = "Android Application for Finding and Declaring Lost or Stray Dogs."
                    internship2 = "Six Month Internship in the Alma Mater Studiorum - Universita di Bologna participating in the Erasmus+ Programme"
                    internshiptitle2 = "Android-based client for a fully-converged edge/fog based architecture for sensing and mobile crowdsensing"
                    />

                <hr style={{borderTop: '3px solid #e22947'}}/>
                <h2>Skills</h2>
                <Skills
                os = "Operating Systems"
                programminglanguages = "Program. Languages"
                frameworks = "Frameworks"
                databases = "Data Bases"
                devopstools = "DevOps Tools"
                tools = "Tools"
                videoeditingsoftware = "Video Editing Software"
                imageeditingsoftware = "Image Editing Software"
                gameengines = "Game Engines"

                os2 = "Windows, Linux"
                programminglanguages2 = "Java, C, C++, C#, Python, HTML, CSS, XML, PHP, JavaScript/jQuery, SQL"     
                frameworks2 = "Bootstrap, Apache Spark, Californium, Selenium"
                databases2 = "MongoDB, Firebase Realtime Database, MySQL, SQLite"
                devopstools2 = "Git"
                tools2 = "Android Studio, CodeBlocks, Eclipse, NetBeans, IntelliJ, Microsoft Visual Studio, PyCharm, Node-RED, Jupyter Notebook"
                videoeditingsoftware2 = "Adobe Premiere, Sony Vegas Pro, Adobe After Effects"
                imageeditingsoftware2 = "Adobe Photoshop, Adobe Illustrator"
                gameengines2 = "Unreal Engine 4, Unity"


                />
                <hr style={{borderTop: '3px solid #e22947'}}/>
                <h2>Languages</h2>

                <Languages
                english = "English"
                spanish = "Spanish"

                english2 = "Level C2"
                spanish2 = "Level B1"


                />
                    </Cell>
                </Grid>
            </div>

        )
    }
}

export default Resume;